/* end of buttons */


.form-control {
    border: 0;
    border-bottom: 1px solid rgba(172, 158, 158, 0.5);
    border-radius: 0;
}
.form-control:focus {
    outline: 0;
    box-shadow: 0 0 2px 2px rgb(164, 109, 201);
    border-color: rgba(172, 158, 158, 0.5);
}
.was-validated .form-control:invalid:focus, .form-control.is-invalid:focus, .was-validated
.custom-select:invalid:focus,
.custom-select.is-invalid:focus {
  box-shadow: 0 -0.08rem 2px 0.08rem rgba(220, 53, 69, 0.25);
}
.was-validated .form-control:valid:focus, .form-control.is-valid:focus, .was-validated
.custom-select:valid:focus,
.custom-select.is-valid:focus {
  border-color: #28a745;
  box-shadow: 0 -0.08rem 2px 0.08rem rgba(40, 167, 69, 0.25);
}


.error {
    background-color: rgb(245, 197, 205);
    border-left: 5px solid rgb(221, 77, 106);
}





.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
    background-color: #7B3CA5;
}
.custom-control-input:checked ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #fff;
}
.custom-control-input:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #fff;
}
.was-validated .custom-control-input:valid ~ .custom-control-label,
.custom-control-input.is-valid ~ .custom-control-label {
    color: rgb(0, 0, 0);
}
.was-validated .custom-control-input:valid ~ .custom-control-label::before, 
.custom-control-input.is-valid ~ .custom-control-label::before {
    background-color: #e9ecef;
}
.was-validated .custom-control-input:valid:checked ~ .custom-control-label::before, 
.custom-control-input.is-valid:checked ~ .custom-control-label::before {
    background-color: rgb(164, 109, 201);
}
.was-validated .custom-control-input:valid:focus ~ .custom-control-label::before, 
.custom-control-input.is-valid:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #fff, 0 0 0 0.2rem rgb(255, 255, 255);
}


